@use 'global' as *;

.p-top-news {
}

.p-top-news__inner {
  padding-left: vw($layout-width-tab, 16);
  @include media(min, md) {
    padding-left: vw($layout-width-canvas, 32);
  }
  @include media(min, xxl) {
    max-width: $layout-width-canvas;
    @include margin-auto;
  }
}

.p-top-news__header {
  padding-right: vw($layout-width-tab, 16);
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include media(min, md) {
    justify-content: flex-start;
    gap: rem(88);
    padding-right: 0;
  }
}

.p-top-news__title {
  font-size: fluid-size(20, 32);
  font-family: $base-font-family-serif;
  line-height: $line-height-none;
  position: relative;

  @include media(min, md) {
    text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.25);
  }

  &::after {
    background: url('#{$pathImages}/title_nws.svg');
    position: absolute;
    content: '';
    width: 115px;
    height: 115px;
    top: -40px;
    right: -95px;
    z-index: map-get($layer, 'decoration');
  }
}

.p-top-news__link a {
  font-size: rem(14);
  font-family: $base-font-family-serif;
  line-height: $line-height-none;
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.25);
  position: relative;
  display: flex;

  &::after {
    content: '';
    width: rem(40);
    height: rem(12);
    border-bottom: solid 1px;
    border-right: solid 1px;
    transform: skew(45deg);
    transition: width $base-duration $base-timing;
  }

  @include hover {
    &::after {
      width: rem(50);
    }
  }
}

.p-top-news__item {
}

.p-top-news__body {
  background: $color-white;
  padding: fluid-size(8, 16);
  display: flex;
  flex-direction: column;
  min-height: rem(93);
}

.p-top-news__text {
  color: $color-black;
  margin-top: rem(8);
  font-size: fluid-size(12, 16);
  @include lh($base-line-height);
}

.p-top-news__img {
  aspect-ratio: 3 / 2;
}

.p-top-news__img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.p-top-news__date {
  color: $color-accent-secondary;
  font-family: $base-font-family-serif;
  font-size: rem(12);
  line-height: $line-height-none;
}

.p-top-news__button-prev,
.p-top-news__button-next {
  height: 50px;
  width: 50px;

  &::after {
    background-repeat: no-repeat;
    background-size: contain;
    content: '';
    height: 50px;
    margin: auto;
    width: 50px;
  }
}

.p-top-news__button-prev {
  &::after {
    background-image: url('#{$pathImages}/slide_button_left.svg');
  }
}

.p-top-news__button-next {
  &::after {
    background-image: url('#{$pathImages}/slide_button_right.svg');
  }
}
