@use 'global' as *;

.p-news-single-content p {
  font-size: rem(14);
  margin-top: rem(40);
  @include lh($base-line-height);
}

.p-news-single-content h2 {
  margin-top: rem(40);
  padding-left: rem(15);
  font-size: rem(18);
  position: relative;
  line-height: $line-height-sm;
  @include lh($line-height-sm);

  &::before {
    content: '';
    position: absolute;
    width: rem(4);
    height: 100%;
    background-color: $color-accent-secondary;
    top: .2em;
    left: rem(-3);
  }
}

.p-news-single-content h3 {
  margin-top: rem(40);
  font-size: rem(16);
  position: relative;
  line-height: $line-height-sm;
  @include lh($line-height-sm);
}
