@use 'global' as *;

/* Card
-------------------------------------------------------------- */

.c-card {
  @include media(max, md) {
    border-bottom: 1px solid $color-accent-secondary;
    padding-bottom: fluid-size(16, 24);
  }
}

.c-card__img {
  aspect-ratio: 3 / 2;
}

.c-card__img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.c-card__body {
  padding: fluid-size(8, 16);
  display: flex;
  flex-direction: column;
}

.c-card__body--primary {
  background-color: $color-white;
}

.c-card__body--secondary {
  background-color: transparent;
}

.c-card__date {
  color: $color-accent-secondary;
  font-family: $base-font-family-serif;
  font-size: rem(12);
  line-height: $line-height-none;
}

.c-card__text {
  margin-top: rem(8);
  font-size: fluid-size(16, 20);
  font-family: $base-font-family-serif;
  @include lh($base-line-height);
}

.c-card__text--primary {
  color: $color-black;
}

.c-card__text--secondary {
  color: $color-white;
}
