@use 'global' as *;

.p-takeout-sec03 {
  border-top: 1px solid $color-accent-secondary;
}

.p-takeout-sec03__list {
  margin-top: rem(24);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: fluid-size(32, 48);

  @include media(min, md) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.p-takeout-sec03__img {
  aspect-ratio: 41 / 25;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    max-width: none;
  }
}

.p-takeout-sec03__title-lv3 {
  font-family: $base-font-family-serif;
  margin-top: rem(16);
  font-weight: $font-weight-bold;
  @include lh($base-line-height);
}

.p-takeout-sec03__price {
  font-family: $base-font-family-serif;
  margin-top: rem(8);
  font-weight: $font-weight-bold;
  @include lh($base-line-height);
}

.p-takeout-sec03__size {
  font-family: $base-font-family-serif;
  margin-top: rem(8);
  font-size: rem(12);
  font-weight: $font-weight-bold;
  @include lh($base-line-height);
}

.p-takeout-sec03__text {
  margin-top: rem(8);
  font-size: rem(12);
  @include lh($base-line-height);
}

.p-takeout-sec03__defi-list {
  margin-top: rem(8);
  @include lh($base-line-height);
}

.p-takeout-sec03_term {
  font-size: rem(12);
}

.p-takeout-sec03__desc {
  font-size: rem(12);
}
