@use 'global' as *;

.p-top-bg {
  position: relative;
}

.p-top-bg__menu01 {
  position: absolute;
  z-index: map-get($layer, decoration);
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  top: 500px;
  & img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }
}

.p-top-bg__menu02 {
  position: absolute;
  z-index: map-get($layer, decoration);
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  top: 650px;
  & img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }
}

.p-top-bg__info01 {
  position: absolute;
  z-index: map-get($layer, decoration);
  bottom: 0;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);

  & img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }
}
