@use 'global' as *;

.p-footer {
  background-color: $base-color-main;
  padding-top: rem(64);
  padding-bottom: rem(25);
  position: relative;

  @include media(min, md) {
    padding-top: rem(80);
    padding-bottom: rem(25);
  }
}

