.p-footer__item-wrap {
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  @include media(min, md) {
    flex-direction: row;
    gap: rem(40);
  }
}

.p-footer__item {
  display: flex;
}

.p-footer__item-left,
.p-footer__item-center {
  display: flex;
  flex-direction: column;
  text-align: center;

  @include media(min, md) {
    gap: rem(16);
    flex-direction: row;
  }
}

.p-footer__logo a img {
  max-width: 100%;
  height: auto;
}

.p-footer__item-center {
  margin-top: rem(56);
  @include media(min, md) {
    margin-top: 0;
  }
}

.p-footer__item-right {
  margin-top: rem(40);

  @include media(min, md) {
    margin-top: 0;
  }
}

.p-footer-address {
  color: $color-white;
  font-size: rem(12);
}

.p-footer-address__shop {
  @include lh($line-height-sm);
  padding-top: rem(16);
  @include media(min, md) {
    padding-top: 0;
  }
}

.p-footer-address__tel {
  margin-top: rem(16);
  @include lh($line-height-sm);
  font-family: $base-font-family-serif;

  span {
    font-size: 133.333333%;
  }
}

.p-footer-address__map {
  margin-top: rem(16);
  @include lh($line-height-sm);
}

.p-footer-address__line {
  margin-top: rem(16);
}

.p-footer-address__map a {
  color: $color-white;
  font-size: rem(12);
  text-decoration: underline;
}

.p-footer__copyright {
  text-align: center;
  margin-top: rem(57);
  color: $color-white;
  font-size: rem(10);
  font-weight: $font-weight-normal;
  line-height: 1;
  letter-spacing: .05em;

  @include media(min, md) {
    margin-top: rem(105);
  }

  & small {
    font-size: fluid-size(12, 14);

    // font-size: rem(12);

    @include media(min, md) {
      font-size: rem(14);
    }
  }
}
