@use 'global' as *;

.p-takeout-sec02 {
  border-top: 1px solid $color-accent-secondary;
}

.p-takeout-sec02__body {
  text-align: center;
}

.p-takeout-sec02__small-warp {
  text-align: center;
}

.p-takeout-sec02__item {
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: fluid-size(24, 75);

  @include media(min, md) {
    flex-direction: row;
  }
}

.p-takeout-sec02__shop {
  font-family: $base-font-family-serif;
  font-size: rem(20);
  font-weight: $font-weight-bold;
  @include lh($base-line-height);
}

.p-takeout-sec02__time {
  font-family: $base-font-family-sans;
  font-size: rem(14);
  margin-top: rem(8);
  @include lh($base-line-height);
}

.p-takeout-sec02__small-warp {
  margin-top: fluid-size(30, 40);
}

.p-takeout-sec02__small {
  @include lh($base-line-height);
}

.p-takeout-sec02__small small {
  font-size: rem(12);
}
