@use 'global' as *;

.p-top-info {
}

.p-top-info__inner {
}

.p-top-info__title-wrap {
  display: grid;
  place-content: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  max-width: 900px;
  border: fluid-size(2, 6) solid $color-accent-secondary;
  padding-top: fluid-size(16, 80);
  padding-bottom: fluid-size(16, 80);
  @include margin-auto;
}

.webp {

  .p-top-info__title-wrap {
    background-image: url('#{$pathImages}/info01.jpg.webp');
  }
}

.no-webp {

  .p-top-info__title-wrap {
    background-image: url('#{$pathImages}/info01.jpg');
  }
}

.p-top-info__title {
  font-size: fluid-size(20, 40);
  font-weight: $font-weight-bold;
  font-family: $base-font-family-serif-Vt;
  color: $color-white;
  writing-mode: vertical-rl;
  text-shadow: 0px 5px 12px rgba(0, 0, 0, .5);
}

.p-top-info__button {
  margin-top: rem(64);
  display: flex;
  flex-direction: column;
  gap: fluid-size(16, 24);

  @include media(min, md) {
    flex-direction: row;
  }
}

.p-top-info__button01 {
  flex: 1 0 48%;
}

.p-top-info__button02 {
  flex: 1 0 48%;
}
