@use 'global' as *;

.p-top-mv-swiper {

  .swiper-slide-active .swiper-img,
  .swiper-slide-duplicate-active .swiper-img,
  .swiper-slide-prev .swiper-img {
    animation: zoomUp 12s linear 0s normal both;
  }
}

.p-top-mv-swiper__wrapper {
}

.p-top-mv-swiper__slide {
}

.p-top-mv-swiper__img {
  position: relative;
  height: calc(100vh - rem($top-mv-height-pc));
  background-color: #ccc;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
}

.webp {

  .p-top-mv-swiper__img--slide01 {
    background-image: url('#{$pathImages}/slide_mv01.jpg.webp');
  }

  .p-top-mv-swiper__img--slide02 {
    background-image: url('#{$pathImages}/slide_mv02.jpg.webp');
  }

  .p-top-mv-swiper__img--slide03 {
    background-image: url('#{$pathImages}/slide_mv02.jpg.webp');
  }
}

.no-webp {

  .p-top-mv-swiper__img--slide01 {
    background-image: url('#{$pathImages}/slide_mv01.jpg');
  }

  .p-top-mv-swiper__img--slide02 {
    background-image: url('#{$pathImages}/slide_mv02.jpg');
  }

  .p-top-mv-swiper__img--slide03 {
    background-image: url('#{$pathImages}/slide_mv02.jpg');
  }
}

.p-top-mv-swiper__pagination {
}
