@use 'global' as *;

.p-initiatives-mv {
  background-color: $base-color-main;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: grid;
  place-content: center;
  height: fluid-size(200, 360);
}

.webp {

  .p-initiatives-mv {
    background-image: url('#{$pathImages}/common/bg12.png.webp');
  }
}

.no-webp {

  .p-initiatives-mv {
    ackground-image: url('#{$pathImages}/common/bg12.png');
  }
}
