@use '../global' as *;

.p-top-menu {
  position: relative;
  z-index: map-get($layer, 'default');
}

.p-top-menu__bg01 {
  position: absolute;
  z-index: map-get($layer, decoration);
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 15%;

  & img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }
}
.p-top-menu__bg02 {
  position: absolute;
  z-index: map-get($layer, decoration);
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 2%;

  & img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }
}
.p-top-menu__bg04 {
  position: absolute;
  z-index: map-get($layer, decoration);
  position: absolute;
  bottom: 0;
  right: 0;

  & img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }
}
.p-top-menu__bg05 {
  position: absolute;
  z-index: map-get($layer, decoration);
  position: absolute;
  left: 0;
  bottom: 0;

  & img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
  }
}
